.addToCart-container {
  width: 298px;
  height: 48px;

  .quantity-div {
    button,
    p {
      width: 40px;
      background-color: $color-grey;
    }
  }
}

.goBack-button {
  margin-bottom: 56px;
  background-color: transparent;
}

.removeCart-button {
  background-color: transparent;

  &:hover {
    cursor: pointer;
    text-decoration: underline;
    color: $color-orange;
  }
}

.shop-button {
  width: 58px;
  height: 18px;
  margin: 15px auto 0;

  p {
    margin-right: 8px;
  }
}

.checkOut-button,
.backToHome-button {
  width: 100%;
  height: 48px;
  background-color: $color-orange;
  color: #fff;

  &:hover {
    background-color: $color-orange-hover;
  }
}

.goHome-button {
  margin: 30px 0 0;
}

.goHome-button,
.reload-button,
.seeProduct-button {
  width: max-content;
  height: 48px;
  background-color: $color-orange;
  color: #fff;
  padding: 0 24px;

  &:hover {
    background-color: $color-orange-hover;
  }
}

